<template>
  <v-row justify="space-around" class="row">
    <v-col lg-3 class="colsKey">
        {{skillName}}
    </v-col>
    <v-col lg-9 class="colsValue">
        <v-progress-linear
            class="progressBar" 
            :value="value">
        </v-progress-linear>
    </v-col>
</v-row>
</template>

<script>


export default {
  name: 'Skill',
  props: {
      skillName: String,
      percentage: String
  },
  data() {
      return {
          value: 0
      }
  },
  mounted() {
      const percentage = Math.ceil(this.percentage/5);
      for(let i= 0;i < 5;i++) {
        setTimeout(() => {      
            this.value += percentage;
            if (i === 4) {
                this.value = this.percentage;
            }
        },500);
      }
  }
}
</script>

<style scoped>

.progressBar {
    transition: width 4s;
}

.colsKey {
    margin-left: 16px;
}

.colsValue {
    margin-right: 8px;
    margin-top: 8px;
}

</style>
