<template>
  <div class="road"></div>
</template>

<script>

export default {
  name: 'Road'
}
</script>

<style scope>

.road {
    position: absolute;
    width: 150%;
    bottom: 20px;
    height: 160px;
    margin-left: -25%;
    background: #525252;
    transform-origin: bottom;
    transform-style: preserve-3d;
    transform: perspective(500px) rotateX(45deg);
}

 .road:before {
    content: '';
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    left: 0;
    width: 100%;
    height: 10px;
    background: linear-gradient(90deg,
    #fff 0%, #fff 50%,
    #525252 50%, #525252 100%);
    background-size: 120px;
    animation: animate 0.5s linear infinite;
}

.road:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 20px;
    background-color: #333;
    bottom: -20px;
    transform-origin: top;
    transform: perspective(500px) rotateX(-40deg);
}


@keyframes animate {
    0% {
        background-position: 0px;
    }
    100% {
        background-position: -120px;
    }
} 

</style>
